import React from 'react';
import './App.css';
import _ from 'lodash';
const { GoogleSpreadsheet } = require('google-spreadsheet');
interface IProps {
}

interface IState {
  rows: IResultRow[];
  isLoading: boolean;
  standings: object;
}

interface IResultRow { 
  Time: string,
  User: string,
  G1: string,
  G1Score: number,
  G1Rank: number,
  G2: string,
  G2Score: number,
  G2Rank: number,
  G3: string,
  G3Score: number,
  G3Rank: number
}
class GolfApp extends React.Component<IProps,IState> {
  constructor(props: IProps) {
    super(props);

    this.state = {
        isLoading: true,
        rows: [],
        standings: {}
    };
}
  
  async componentDidMount() {
    const doc = new GoogleSpreadsheet('1cBH5gIIXHI0S8zW5Hb6_9ReAkbtAK0OJsiNXfBPbyos');
    doc.useApiKey('AIzaSyAbBjPSTC_UlABjvDGH0P4c7IhUS9Kwcw4');
    await doc.loadInfo();
    const sheet = doc.sheetsByIndex[0];
    const rows = await sheet.getRows();

    let leaderboard = await fetch('https://gripapi-static-pd.usopen.com/gripapi/leaderboard.json');
    let data = await leaderboard.json();
    //console.log(data);
    
    let standings = data.standings;
    let newstandings = {};    
    for(let i=0;i < standings.length; i++) {
      let player : string = standings[i].player.firstName + " " + standings[i].player.lastName;
      let displayRank = standings[i].position.displayValue;
      let rank = displayRank[0] == 'T' ? displayRank.substring(1)*1 : displayRank*1;
      let score = standings[i].toPar.displayValue;
      newstandings[player] = {rank, displayRank, score};
    }
    console.log(standings);
    console.log(newstandings);
    console.log()
    this.setState({
      isLoading: false,
      rows: rows,
      standings: newstandings
    });
    
  }
  render() {
    let results = this.state.rows;
    if(this.state.isLoading) {
      return (
        <div>Loading...</div>
      );
    }
    else {
      let standings = this.state.standings;
      results = results.sort((b,a) => (standings[b.G1].rank+standings[b.G2].rank+standings[b.G3].rank) - (standings[a.G1].rank+standings[a.G2].rank+standings[a.G3].rank)  )
      return (
        <table>
          <tr><th>Name</th><th colSpan={3}>Pick 1</th><th colSpan={3}>Pick 2</th><th colSpan={3}>Pick 3</th><th>Score</th></tr>
          {results.map(player => (
            <tr>
            <td>{ player.User }</td>
            <td>{ player.G1 }</td>
            <td>{standings[player.G1].score }</td>
            <td style={{
                        textAlign:"center"
                      }}>{ standings[player.G1].displayRank}</td>
            <td>{ player.G2 }</td>
            <td>{standings[player.G2].score }</td>
            <td style={{
                        textAlign:"center"
                      }}>{ standings[player.G2].displayRank}</td>
            <td> { player.G3 }</td>
            <td>{standings[player.G3].score }</td>
            <td style={{
                        textAlign:"center"
                      }}>{ standings[player.G3].displayRank}</td>
            <td style={{
                        textAlign:"center"
                      }}>{ standings[player.G1].rank+standings[player.G2].rank+standings[player.G3].rank}</td>
            </tr>
          ))}
        </table>
      );
    }
  }
}

export default GolfApp;
